import { useState } from "react"
import {
    Table,
    TableHeader,
    TableBody,
    TableColumn,
    TableRow,
    TableCell
  } from "@nextui-org/table";
  import {
    Modal, 
    ModalContent, 
    ModalHeader, 
    ModalBody, 
    ModalFooter,
    useDisclosure
  } from "@nextui-org/modal";
  import {Accordion, AccordionItem} from "@nextui-org/accordion";

  import {Tooltip} from "@nextui-org/tooltip";
  import Image from 'next/image'
  import Link from 'next/link'
  import Edit from '@/public/images/edit.svg'
  import View from '@/public/images/view.png'
  import Trash from '@/public/images/lixeira.png'
import { Button } from "@nextui-org/button";


  type StudentType = {
    id: string,
    name: string,
    cpf: string,
    email: string,
    course: string,
  }


export function BoxStudent( {  }) {

    const studens: StudentType[] = [
        {
            id: '1',
            name: "Thiago Araujo",
            cpf: "16953214724",
            email: "thiagobarbosa.araujo@outlook.com",
            course: "automação"
        },

        {   
            id: '2',
            name: "Niely Alves",
            cpf: "1506325874",
            email: "niely@outlook.com",
            course: "instalação elétrica"
        },

        {
            id: '3',
            name: "Marilene Barbosa",
            cpf: "76525846893",
            email: "marilene@outlook.com",
            course: "comandos elétricos",
        },
    ]

    const [students, setStudents] = useState<string>('')
    const [studentId, setStudentId] = useState<string>('')

    const {isOpen, onOpen, onOpenChange} = useDisclosure();

    function deleteStudent(id: string) {
        console.log(id)
    }

    return (
        <>
            <Table className="flex-1" removeWrapper>
            <TableHeader>
                <TableColumn>NOME</TableColumn>
                <TableColumn>CPF</TableColumn>
                <TableColumn>EMAIL</TableColumn>
                <TableColumn>COURSE</TableColumn>
                <TableColumn>Actions</TableColumn>
            </TableHeader>
        
            <TableBody>
                {studens.map((student, index) => (
                    <TableRow key={index}>
                        <TableCell>
                            <Accordion>
                                <AccordionItem key={index} aria-label="Accordion 1" title="Accordion 1">
                                    {student.name} 
                                </AccordionItem>
                                <AccordionItem key={index} aria-label="Accordion 1" title="Accordion 1">
                                    {student.cpf} 
                                </AccordionItem>
                                <AccordionItem key={index} aria-label="Accordion 1" title="Accordion 1">
                                    {student.email}
                                </AccordionItem>
                                <AccordionItem key={index} aria-label="Accordion 1" title="Accordion 1">
                                    {student.course}
                                </AccordionItem>
                                <AccordionItem key={index} aria-label="Accordion 1" title="Accordion 1">
                                <Tooltip color="primary" content="Infos">
                                    <Link href="/student"><Image width={24} height={24} src={View} alt='imagem de visualização' /></Link>
                                </Tooltip>
                            
                                <Tooltip color="primary" content="Editar">
                                    <Image className="cursor-pointer"  width={24} height={24} src={Edit} alt='imagem de visualização' />
                                </Tooltip>

                                <Tooltip color="danger" content="Excluir">
                                    <span onClick={() => setStudentId(student.id)}>
                                        <Image onClick={onOpen} className="cursor-pointer"  width={24} height={24} src={Trash} alt='imagem de exclusão' />
                                    </span>
                                </Tooltip>
                                </AccordionItem>
                            </Accordion>
                        </TableCell>
                    </TableRow>
                ))}
            </TableBody>
            </Table>
            
            <Modal isOpen={isOpen} onOpenChange={onOpenChange}>
                <ModalContent>
                {(onClose) => (
                    <>
                    <ModalHeader className="flex flex-col gap-1">Excluir Aluno</ModalHeader>
                    <ModalBody>
                        <p> 
                            Tem certeza que deseja excluir esse aluno ?
                        </p>
                        
                    </ModalBody>
                    <ModalFooter>
                       
                        <Button color="primary" onPress={onClose}>
                            Fechar
                        </Button>

                        <Button color="danger" variant="light" onClick={() => deleteStudent(studentId)}  onPress={onClose}>
                            Excluir
                        </Button>
                    </ModalFooter>
                    </>
                )}
                </ModalContent>
            </Modal>
        </>
    )
}

